import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import InputLabel from "@material-ui/core/InputLabel";
import "./ManageBrand.css";
import { Popconfirm, message } from "antd";
import Axios from "axios";
import moment from "moment";
import { Link } from "react-router-dom";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import { Button, Select } from "@material-ui/core";
import FormControl from "@material-ui/core/FormControl";
import TextField from "@material-ui/core/TextField";
import { EditFilled } from "@ant-design/icons";
import { DeleteFilled } from "@ant-design/icons";
import Dropdown from "../../../components/Dropdown/Dropdown";

const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    maxWidth: 200,
    marginLeft: 20,
  },
}));

const ManageBrand = () => {
  useEffect(() => {
    fetchData();
    fetchCategoryData();
    fetchEditCategoryData();
  }, []);
  const [brandName, setBrandName] = useState("");
  const [CategoryData, setCategoryData] = useState([]);
  const [category, setCategory] = useState("");
  const [open, setOpen] = React.useState(false);
  const [editOpen, setEditOpen] = React.useState(false);
  const [editCategory, setEditCategory] = useState("");
  const [editBrand, setEditBrand] = useState("");
  const [editBrandId, setEditBrandId] = useState("");
  const [editCategoryData, setEditCategoryData] = useState([]);

  const classes = useStyles();

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  const handleClickEditOpen = (editBrandId) => {
    setEditOpen(true);
    fetchEditData(editBrandId);
  };

  const handleEditClose = () => {
    setEditOpen(false);
  };

  const [data, setData] = useState([]);

  const fetchCategoryData = () => {
    Axios.get("/category/product")
      .then((data) => {
        setCategoryData(data.data);
        console.log(data.data);
      })
      .catch((err) => console.log(err));
  };
  let CategoryOptions = CategoryData.map((s, i) => {
    return <option value={s.id}>{s.name}</option>;
  });

  const fetchData = () => {
    Axios.get("/brand/")
      .then((data) => {
        setData(data.data.data);
        console.log(data.data.data);
      })
      .catch((err) => console.log(err));
  };

  function deleteBrand(brand_id) {
    Axios.delete("/brand/" + brand_id)
      .then((data) => {
        message.success("Brand Deleted Successfully");
        fetchData();
      })
      .catch((err) => console.log(err));
  }
  const save = (e) => {
    e.preventDefault();
    Axios.post("/brand/", {
      brandName: brandName,
      category: category,
    })
      .then((data) => {
        message.success(data.data.message);
        console.log(data);
        handleClose();
        fetchData();
        setCategory("");
        setBrandName("");
      })
      .catch((err) => alert("Somthing went wrong"));
  };

  const fetchEditData = (editBrandId) => {
    try {
      Axios.get("/brand/category/" + editBrandId)
        // Axios.get("/brand/category/" + id)
        .then((data) => {
          console.log(data.data.data[0]);
          let d = data.data.data[0];
          setEditBrand(d.brand_name);
          setEditBrandId(d.brand_id);
          setEditCategory(d.category_id);
        })
        .catch((err) => console.log(err));
    } catch (err) {
      alert("Network error");
    }
  };

  const fetchEditCategoryData = () => {
    Axios.get("/category/product")
      .then((data) => {
        console.log("Category", data.data);
        setEditCategoryData(data.data);
      })
      .catch((err) => console.log(err));
  };

  const Update = (e) => {
    Axios.put("/brand/Update", {
      category_id: editCategory,
      brand_name: editBrand,
      brand_id: editBrandId,
    })
      .then((data) => {
        console.log(data);
        alert("Brand Updated Successfully");
        handleEditClose();
        fetchData();
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <div className="module-main-div">
      <div style={{ marginLeft: 960 }}>
        <Button variant="contained" color="primary" onClick={handleClickOpen}>
          Add
        </Button>
        <Dialog
          open={open}
          onClose={handleClose}
          aria-labelledby="form-dialog-title"
        >
          <DialogTitle id="form-dialog-title">Add New Brand</DialogTitle>

          <FormControl variant="outlined" className={classes.formControl}>
            <InputLabel id="demo-simple-select-outlined-label">
              Category
            </InputLabel>
            <Select
              labelId="demo-simple-select-outlined-label"
              id="demo-simple-select-outlined"
              onChange={(value) => {
                setCategory(value.target.value);
              }}
              label="Category"
            >
              {CategoryOptions}
            </Select>
          </FormControl>

          <DialogContent style={{ width: "100%" }}>
            <TextField
              autoFocus
              margin="dense"
              id="name"
              label="Brand"
              type="text"
              style={{ marginTop: 20 }}
              onChange={(e) => setBrandName(e.target.value)}
            />
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose} color="primary">
              Cancel
            </Button>
            <Button
              onClick={(e) => {
                save(e);
              }}
              color="primary"
            >
              Submit
            </Button>
          </DialogActions>
        </Dialog>
      </div>
      <div>
        <Dialog
          open={editOpen}
          onClose={handleClose}
          aria-labelledby="form-dialog-title"
        >
          <DialogTitle id="form-dialog-title">Update Brand</DialogTitle>
          <FormControl variant="outlined" className={classes.formControl}>
            <InputLabel id="demo-simple-select-outlined-label">
              Category
            </InputLabel>
            <Select
              labelId="demo-simple-select-outlined-label"
              id="demo-simple-select-outlined"
              onChange={(value) => {
                setEditCategory(value.target.value);
              }}
              label="Category"
              value={editCategory}
            >
              {CategoryOptions}
            </Select>
          </FormControl>
          <DialogContent>
            <TextField
              autoFocus
              margin="dense"
              id="name"
              label="Brand Name"
              type="text"
              value={editBrand}
              onChange={(e) => setEditBrand(e.target.value)}
            />
          </DialogContent>
          <DialogActions>
            <Button onClick={handleEditClose} color="primary">
              Cancel
            </Button>
            <Button
              onClick={() => {
                Update();
              }}
              color="primary"
            >
              Update
            </Button>
          </DialogActions>
        </Dialog>
      </div>
      <h1 style={{ marginTop: -38 }} className="module-title">
        Brand
      </h1>

      <div className="module-inner-div-manage">
        <div id="manage-table">
          <div id="manage-table-header">
            <div className="header-inner-div-header">
              <p>Id</p>
            </div>

            <div className="header-inner-div-header">
              <p>Category Name</p>
            </div>
            <div className="header-inner-div-header">
              <p>Brand Name</p>
            </div>
            <div className="header-inner-div-header">
              <p>Created Date</p>
            </div>
            <div className="header-inner-div-header">
              <p>Edit</p>
            </div>
            <div className="header-inner-div-header">
              <p>Delete</p>
            </div>
          </div>
          <div className="manage-table-body">
            {data.map((item, i) => {
              return (
                <div className="manage-table-body-particular">
                  <div className="body-inner-div-body">
                    <p>{i + 1}</p>
                  </div>

                  <div className="body-inner-div-body">
                    <p>{item.category_name}</p>
                  </div>
                  <div className="body-inner-div-body">
                    <p>{item.brand_name}</p>
                  </div>
                  <div className="body-inner-div-body">
                    <p>{moment(item.crte_dt).format("DD-MM-YYYY")}</p>
                  </div>
                  <div className="body-inner-div-body" style={{ fontSize: 20 }}>
                    <Button
                      align="center"
                      style={{ fontSize: 20 }}
                      onClick={() => {
                        handleClickEditOpen(item.brand_id);
                        setEditBrandId(item.brand_id);
                      }}
                    >
                      <EditFilled />
                    </Button>
                  </div>
                  <Popconfirm
                    title="Are you sure to delete this Brand?"
                    onConfirm={() => deleteBrand(item.brand_id)}
                    onCancel={null}
                    okText="Yes"
                    cancelText="No"
                  >
                    <div
                      className="body-inner-div-body"
                      style={{ fontSize: 20 }}
                    >
                      <DeleteFilled />
                    </div>
                  </Popconfirm>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ManageBrand;
