import React, { useState, useEffect } from "react";
import "./ManageCategory.css";
import { Popconfirm, message } from "antd";
import Axios from "axios";
import moment from "moment";
import { Link } from "react-router-dom";
import Input from "../../../components/Input/Input";
import TextField from "@material-ui/core/TextField";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import { Button } from "@material-ui/core";
import { EditFilled } from "@ant-design/icons";
import { DeleteFilled } from "@ant-design/icons";

const ManageCategory = () => {
  useEffect(() => {
    fetchData();
  }, []);
  const [categoryName, setCategoryName] = useState("");
  const [open, setOpen] = React.useState(false);
  const [editOpen, setEditOpen] = React.useState(false);
  const [data, setData] = useState([]);
  const [categoryId, setCategoryId] = useState("");
  const [editId, setEditId] = useState(0);
  const [editCategoryId, setEditCategoryId] = useState("");
  const [editCategoryName, setEditCategoryName] = useState("");

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  const handleClickEditOpen = (id) => {
    setEditOpen(true);
    fetchDataList(id);
  };

  const handleEditClose = () => {
    setEditOpen(false);
  };

  const fetchDataList = (id) => {
    try {
      Axios.get("/category/" + id)
        .then((data) => {
          console.log(data.data.data[0]);
          let d = data.data.data[0];
          setEditCategoryName(d.category_name);
          setEditCategoryId(d.category_id);
        })
        .catch((err) => console.log(err));
    } catch (err) {
      alert("Network error");
    }
  };

  const addPost = async (e) => {
    e.preventDefault();
    Axios.post("/category/v1/catalog/categories/add", {
      category_name: categoryName,
    })
      .then((data) => {
        message.warn(data.data.message);
        console.log(data);
        handleClose();
        fetchData();
        setCategoryName("");
      })
      .catch((err) => alert("Somthing went wrong"));
  };

  const fetchData = () => {
    Axios.get("/category/")
      .then((data) => {
        setData(data.data.data);
        console.log(data.data.data);
      })
      .catch((err) => console.log(err));
  };

  function deleteCategory(category_id) {
    Axios.delete("/category/" + category_id)
      .then((data) => {
        message.success("Category Deleted Successfully");
        fetchData();
      })
      .catch((err) => console.log(err));
  }

  const update = () => {
    Axios.put("/category/Update", {
      category_name: editCategoryName,
      category_id: editCategoryId,
    })
      .then((data) => {
        alert("category Updated Successfully");
        handleEditClose();
        fetchData();
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <div className="module-main-div">
      <div id="table-inner-div">
        <div style={{ marginLeft: 960 }}>
          <Button variant="contained" color="primary" onClick={handleClickOpen}>
            Add
          </Button>
        </div>
        <Dialog
          open={open}
          onClose={handleClose}
          aria-labelledby="form-dialog-title"
        >
          <DialogTitle id="form-dialog-title">Add New Category</DialogTitle>
          <DialogContent>
            <TextField
              autoFocus
              margin="dense"
              id="name"
              label="Category Name"
              type="text"
              style={{ width: "100%" }}
              value={categoryName}
              onChange={(e) => setCategoryName(e.target.value)}
            />
            <Input
              style={{ width: "100%" }}
              autoFocus={false}
              label="Category Image"
              type="file"
            />
          </DialogContent>
          <DialogActions>
            <Button
              onClick={handleClose}
              variant="contained"
              color="primary"
              color="primary"
            >
              Cancel
            </Button>
            <Button
              onClick={(e) => {
                addPost(e);
              }}
              color="primary"
              variant="contained"
              color="primary"
            >
              Submit
            </Button>
          </DialogActions>
        </Dialog>
      </div>
      <div>
        <Dialog
          open={editOpen}
          onClose={handleClose}
          aria-labelledby="form-dialog-title"
        >
          <DialogTitle id="form-dialog-title">Update Category</DialogTitle>
          <DialogContent>
            <TextField
              autoFocus
              margin="dense"
              id="name"
              label="Category Name"
              type="text"
              value={editCategoryName}
              onChange={(e) => setEditCategoryName(e.target.value)}
            />
          </DialogContent>
          <DialogActions>
            <Button onClick={handleEditClose} color="primary">
              Cancel
            </Button>
            <Button
              onClick={() => {
                update();
              }}
              color="primary"
            >
              Update
            </Button>
          </DialogActions>
        </Dialog>
      </div>
      <h1 style={{ marginTop: -38 }} className="module-title">
        Category
      </h1>

      <div className="module-inner-div-manage">
        <div id="manage-table">
          <div id="manage-table-header">
            <div className="header-inner-div-header">
              <p>Id</p>
            </div>
            <div className="header-inner-div-header">
              <p>Image</p>
            </div>
            <div className="header-inner-div-header">
              <p>Name</p>
            </div>
            <div className="header-inner-div-header">
              <p>Created Date</p>
            </div>
            <div className="header-inner-div-header">
              <p>Edit</p>
            </div>
            <div className="header-inner-div-header">
              <p>Delete</p>
            </div>
          </div>
          <div className="manage-table-body">
            {data.map((item, i) => {
              return (
                <div className="manage-table-body-particular">
                  <div className="body-inner-div-body">
                    <p>{i + 1}</p>
                  </div>
                  <div className="body-inner-div-body">
                    <img
                      src={item.category_image}
                      alt={item.category_name}
                      id="manage-img"
                    />
                  </div>
                  <div className="body-inner-div-body">
                    <p>{item.category_name}</p>
                  </div>
                  <div className="body-inner-div-body">
                    <p>{moment(item.crte_dt).format("DD-MM-YYYY")}</p>
                  </div>
                  <div className="body-inner-div-body" style={{ fontSize: 20 }}>
                    <Button
                      align="center"
                      style={{ fontSize: 20 }}
                      onClick={() => {
                        handleClickEditOpen(item.category_id);
                        setEditId(item.category_id);
                      }}
                    >
                      <EditFilled />
                    </Button>
                  </div>
                  <Popconfirm
                    title="Are you sure to delete this category?"
                    onConfirm={() => deleteCategory(item.category_id)}
                    onCancel={null}
                    okText="Yes"
                    cancelText="No"
                  >
                    <div
                      className="body-inner-div-body"
                      style={{ fontSize: 20 }}
                    >
                      <DeleteFilled />
                    </div>
                  </Popconfirm>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ManageCategory;
